.reviews {
  margin: 0;
  padding: 0;
  width: 320px;
  min-height: 444px;
}

.reviews__list {
  list-style: none;
}

.reviews__item {
  text-align: center;
}

.reviews__container {
  width: 280px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.reviews__name {
  font-family: "Open Sans", Arial, sans-serif;
  font-weight: bold;
  font-size: 18px;
  line-height: 30px;
  display: inline-block;
  margin: 0;
  padding: 0;
}

.reviews__text {
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 14px;
  line-height: 30px;
  color: #283645;
  margin: 0;
  padding: 0;
}

.reviews__age-and-work {
  display: none;
}

.reviews__next,
.reviews__back {
  display: none;
}

@media (min-width: 660px) {

  .reviews__container {
    width: 620px;
  }

  .reviews__age-and-work {
    display: block;
    font-family: "Open Sans", Arial, sans-serif;
    font-size: 14px;
    line-height: 30px;
    color: #283645;
    order: 2;
  }

  .reviews__item {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }

  .reviews__text {
    order: 0;
  }

  .reviews__name {
    order: 1;
  }

}

@media (min-width: 1200px) {

  .reviews__container {
    width: 940px;
    padding: 0 130px;
    padding-top: 186px;
    position: relative;
  }

  .reviews__container::before {
    content: url(../img/quotes.svg);
    position: absolute;
    left: 45%;
    top: 70px;
  }

  .reviews__text {
    font-size: 18px;
    width: 614px;
    margin-left: 168px;
  }

  .reviews__next {
    display: block;
    border: none;
    background-color: rgba(0, 0, 0, 0);
    position: absolute;
    top: 200px;
    right: 135px;
    z-index: 1;
  }

  .reviews__back {
    display: block;
    border: none;
    background-color: rgba(0, 0, 0, 0);
    position: absolute;
    top: 200px;
    left: 135px;
    z-index: 1;
  }

  .pagination__wrapper {
    display: none;
  }

}
